import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import * as providers from '../../providers';

@IonicPage({
  name: 'ResetPasswordPage',
  segment: 'reset-password'
})
@Component({
  selector: 'page-reset-password',
  templateUrl: 'reset-password.html',
})
export class ResetPasswordPage {

  resetAccount: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    private loading: providers.AppLoadingProvider,
    private passwordService: providers.PasswordServiceProvider) {
      this.resetAccount = {};
    }

  doRequest() {

    this.loading.start(() => {
      
      return this.passwordService.reset(this.resetAccount.email).toPromise()
      .then(() => {
        
        let alert = this.alertCtrl.create({
          title: '情報',
          subTitle: 'パスワードのリセットを受付ました。メールを確認して、パスワードを再登録してください。',
          buttons: ['OK']
        });
        alert.present();
        
      }).catch((response) => {
        let alert = this.alertCtrl.create({
          title: 'エラー',
          subTitle: 'パスワードのリセットに失敗しました。',
          buttons: ['OK']
        });
        alert.present();
      });
    });
  }
}



